import './itemsData.css';

const pt = 0.75; //     
const items = {
  img: (args) => (
    <img id='img' className='item' alt={args.alt} src={args.src} />
  ),
  button: (args) => (
    <button
      className='item'
      style={{
        backgroundImage: `url(${args.src})`,
        fontSize: `${args.sizeFont / pt}px`,
      }}
    >
      {args.text}
    </button>
  ),
  text: (args) => <div>{args.text}</div>,
};

export default items;
