import { changeProperties } from '../slices/slicePropertiesElement';

async function base64EncodeImage(file) {
  const reader = new FileReader();
  reader.readAsDataURL(file);
  await new Promise((resolve, reject) => {
    reader.onload = () => {
      resolve(reader.result);
    };
    reader.onerror = (error) => {
      reject(error);
    };
  });
  return reader.result;
}

const handleInputFileChange = (dispatch) => (event) => {
  if (event.target.files[0]) {
    const file = event.target.files[0];
    const reader = new FileReader();
    reader.onloadend = async () => {
      const base64Data = await base64EncodeImage(file);
      dispatch(changeProperties({ src: base64Data }));
    };
    reader.readAsArrayBuffer(file);
  }
};

export { base64EncodeImage, handleInputFileChange };
