import { useDispatch, useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { useEffect, useState } from 'react';
import Moveable from 'react-moveable';
import { useDrop } from 'react-dnd';
import { addElements, updateElements } from '../../slices/sliceElements';
import items from '../shared/itemsData';
import './Display.css';

const pt = 0.75; //     
const Display = () => {
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const {
    properties: { properties },
    elements: { elements },
  } = useSelector((state) => state.data);
  const [stateDrop, setStateDrop] = useState();
  const [snappable, setSnappable] = useState();
  const arrayElements = Object.values(elements);

  useEffect(() => {
    const newSnappable = [
      ...Object.keys(elements).map((item) => `#${item}`),
      '#snapContainer',
    ];
    setSnappable(newSnappable);
  }, [elements]);

  useEffect(() => {
    if (
      stateDrop &&
      !arrayElements.find((item) => item.id === properties.name)
    ) {
      const [x, y] = stateDrop;
      const newElement = {
        ...properties,
        id: properties.name,
        locationX: x,
        locationY: y,
      };
      dispatch(addElements(newElement));
    } else if (arrayElements.length > 0) {
      //    toast 
      // eslint-disable-next-line no-alert
      alert(t('error.alert'));
    }
  }, [stateDrop]);

  const [, drop] = useDrop(() => ({
    accept: 'box',
    collect: (monitor) => ({
      isActive: monitor.canDrop() && monitor.isOver(),
    }),
    drop: (props, monitor) => {
      const clientOffset = monitor.getClientOffset();
      const container = document.getElementById('snapContainer');
      setStateDrop([
        clientOffset.x - container.offsetLeft,
        clientOffset.y - container.offsetTop,
      ]);
      return {
        backgroundColor: 'lightblue',
      };
    },
  }));

  return (
    <div className='display'>
      <div ref={drop} className='dropList'>
        <div id='snapContainer' className='snapContainer'>
          {arrayElements.map((element) => (
            <div key={element.id}>
              <div
                id={element.id}
                className='element'
                style={{
                  width: `${element.width}px`,
                  height: `${element.height}px`,
                  fontSize: `${element.sizeFont / pt}px`,
                  transformOrigin: `${element.pointX}% ${element.pointY}%`,
                  transform: `translate(${element.locationX}px, ${element.locationY}px) rotate(${element.rotation}deg)`,
                }}
              >
                {items[element.type]({
                  ...element,
                  alt: t('interface.uploadImage'),
                })}
              </div>
              <Moveable
                target={`#${element.id}`}
                snapGap
                draggable
                resizable
                rotatable
                snappable
                isDisplaySnapDigit
                snapThreshold={5}
                elementGuidelines={snappable}
                snapContainer='.snapContainer'
                snapDirections={{
                  top: true,
                  left: true,
                  bottom: true,
                  right: true,
                  center: true,
                  middle: true,
                }}
                elementSnapDirections={{
                  top: true,
                  left: true,
                  bottom: true,
                  right: true,
                  center: true,
                  middle: true,
                }}
                bounds={{
                  left: 0,
                  top: 0,
                  right: 0,
                  bottom: 0,
                  position: 'css',
                }}
                onDrag={(e) => {
                  e.target.style.transform = e.transform;
                }}
                onDragEnd={(e) => {
                  if (e.lastEvent) {
                    dispatch(
                      updateElements({
                        id: element.id,
                        locationX: e.lastEvent.translate[0],
                        locationY: e.lastEvent.translate[1],
                      }),
                    );
                  }
                }}
                onResize={(e) => {
                  e.target.style.width = `${e.width}px`;
                  e.target.style.height = `${e.height}px`;
                  e.target.style.transform = e.drag.transform;
                }}
                onResizeEnd={(e) => {
                  const rect = e.moveable.getRect();
                  dispatch(
                    updateElements({
                      id: element.id,
                      width: rect.offsetWidth,
                      height: rect.offsetHeight,
                    }),
                  );
                }}
                onRotate={(e) => {
                  e.target.style.transform = e.drag.transform;
                }}
                onRotateEnd={(e) => {
                  const rect = e.moveable.getRect();
                  dispatch(
                    updateElements({
                      id: element.id,
                      rotation: rect.rotation,
                    }),
                  );
                }}
              />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Display;
