import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import { changeProperties } from '../../slices/slicePropertiesElement';
import StartItem from '../startItem/StartItem';
import Img from '../img/Img';
import Button from '../button/Button';
import Text from '../text/Text';
import './LeftMenu.css';

const LefMenu = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const {
    properties: {
      properties: { name, width, height, rotation, pointX, pointY, sizeFont },
    },
  } = useSelector((state) => state.data);
  const type = {
    img: Img,
    button: Button,
    text: Text,
  };
  const types = Object.keys(type);
  const [typeElement, setTypeElement] = useState(types[0]);
  const CorrectElement = type[typeElement];

  useEffect(() => {
    dispatch(changeProperties({ type: typeElement }));
  }, [typeElement]);

  const onChange =
    (nameProperty) =>
    ({ target: { value } }) => {
      dispatch(changeProperties({ [nameProperty]: value }));
    };

  const onChangeElement = (e) => {
    setTypeElement(e.target.value);
  };

  return (
    <div className='leftMenu'>
      <StartItem typeElement={typeElement} />
      <div className='containerProperties'>
        <div>
          <label htmlFor='select'>{t('interface.typeElement')}</label>
          <select onChange={onChangeElement} id='select' type='select'>
            {types.map((item) => (
              <option value={item} key={item}>
                {item}
              </option>
            ))}
          </select>
        </div>
        {typeElement && <CorrectElement />}
        <div>
          <label htmlFor='name'>{t('interface.name')}</label>
          <input
            type='text'
            id='name'
            value={name}
            onChange={onChange('name')}
          />
        </div>
        <div>
          <label htmlFor='width'>{t('interface.width')}</label>
          <input
            type='text'
            id='width'
            value={width}
            onChange={onChange('width')}
          />
        </div>
        <div>
          <label htmlFor='height'>{t('interface.height')}</label>
          <input
            type='text'
            id='height'
            value={height}
            onChange={onChange('height')}
          />
        </div>
        <div>
          <label htmlFor='rotation'>{t('interface.rotation')}</label>
          <input
            type='text'
            id='rotation'
            value={rotation}
            onChange={onChange('rotation')}
          />
        </div>
        <div>
          <label htmlFor='pointX'>{t('interface.pointX')}</label>
          <input
            type='text'
            id='pointX'
            value={pointX}
            onChange={onChange('pointX')}
            placeholder={t('interface.pointPlaceholder')}
          />
        </div>
        <div>
          <label htmlFor='pointY'>{t('interface.pointY')}</label>
          <input
            type='text'
            id='pointY'
            value={pointY}
            onChange={onChange('pointY')}
            placeholder={t('interface.pointPlaceholder')}
          />
        </div>
        <div>
          <label htmlFor='sizeFont'>{t('interface.sizeFont')}</label>
          <input
            type='text'
            id='sizeFont'
            value={sizeFont}
            onChange={onChange('sizeFont')}
          />
        </div>
      </div>
    </div>
  );
};
export default LefMenu;
