import { useDispatch, useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { changeProperties } from '../../slices/slicePropertiesElement';
import { handleInputFileChange } from '../../utils/imageToJSON';

const Button = () => {
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const {
    properties: {
      properties: { text },
    },
  } = useSelector((state) => state.data);

  const onChange = ({ target: { value } }) => {
    dispatch(changeProperties({ text: value }));
  };

  return (
    <div className='containerProperties'>
      <div>
        <input
          type='file'
          accept='image/*'
          onChange={handleInputFileChange(dispatch)}
        />
      </div>
      <div>
        <label htmlFor='textButton'>{t('interface.text')}</label>
        <input type='text' id='text' value={text} onChange={onChange} />
      </div>
    </div>
  );
};

export default Button;
