import { useDispatch } from 'react-redux';
import { handleInputFileChange } from '../../utils/imageToJSON';

const Img = () => {
  const dispatch = useDispatch();

  return (
    <div>
      <input
        type='file'
        accept='image/*'
        onChange={handleInputFileChange(dispatch)}
      />
    </div>
  );
};

export default Img;
