import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useDrag } from 'react-dnd';
import { useSelector } from 'react-redux';
import items from '../shared/itemsData';
import './StartItem.css';

const pt = 0.75; //     
const point = 2.5; //   
const StartItem = (props) => {
  const { typeElement } = props;
  const { t } = useTranslation();
  const {
    properties: { properties },
  } = useSelector((state) => state.data);
  const { width, height, rotation, pointX, pointY, sizeFont } = properties;

  const [{ isDragging }, drag] = useDrag(() => ({
    type: 'box',
    item: {},
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  }));

  const [styleItem, setStyleItem] = useState({
    transformOrigin: `${pointX}% ${pointY}%`,
    rotate: `${rotation}deg`,
    fontSize: `${sizeFont / pt}px`,
    width: `${width}px`,
    opacity: isDragging ? 0 : 1,
    height: `${height}px`,
  });
  const [stylePoint, setStylePoint] = useState({
    top: `calc(${pointX}% - 2.5px)`,
    left: `calc(${pointY}% - 2.5px)`,
  });

  useEffect(() => {
    const newStyleItem = {
      ...styleItem,
      transformOrigin: `${pointX}% ${pointY}%`,
      width: `${width}px`,
      height: `${height}px`,
      rotate: `${rotation}deg`,
      fontSize: `${sizeFont / pt}px`,
    };
    setStyleItem(newStyleItem);
    setStylePoint({
      top: `calc(${pointY}% - ${point}px)`,
      left: `calc(${pointX}% - ${point}px)`,
    });
  }, [width, height, rotation, pointX, pointY, sizeFont]);

  return (
    <div className='startItem' draggable ref={drag} style={styleItem}>
      {items[typeElement]({ ...properties, alt: t('interface.uploadImage') })}
      <div className='pointRotation' style={stylePoint} />
    </div>
  );
};
export default StartItem;
