/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  properties: {
    name: '',
    text: '',
    width: 50,
    height: 50,
    rotation: 0,
    pointX: 0,
    pointY: 0,
    sizeFont: 0,
    src: '',
    type: '',
  },
};

const sliceProperties = createSlice({
  name: 'sliceProperties',
  initialState,
  reducers: {
    changeProperties: (state, { payload }) => {
      const newProperties = { ...state.properties, ...payload };
      state.properties = newProperties;
    },
  },
});

export const { changeProperties } = sliceProperties.actions;

export default sliceProperties.reducer;
