/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  elements: {},
};

const sliceElements = createSlice({
  name: 'sliceElements',
  initialState,
  reducers: {
    addElements: (state, { payload }) => {
      state.elements[payload.id] = payload;
    },
    updateElements: (state, { payload }) => {
      const newElement = {
        ...state.elements[payload.id],
        ...payload,
      };
      state.elements[payload.id] = newElement;
    },
  },
});

export const { updateElements, addElements, removeElement } =
  sliceElements.actions;

export default sliceElements.reducer;
